import * as React from 'react';
import { AssemblySectionsTitle, sectionSteps} from "src/guide/constants/assembly";

class Assembly extends React.Component {

  public render(): JSX.Element {
    return (
      <div className="text-justify">
        {this.renderSteps(AssemblySectionsTitle.FrameAssembly)}
      </div>
    )
  }

  private renderSteps = (section: string) => {
    const steps = [];
    const sectionKey = Object.keys(AssemblySectionsTitle).find(key =>
        AssemblySectionsTitle[key] === section) as string;
    const sectionData = sectionSteps[sectionKey];

    for (const step in sectionData) {
      if (sectionData.hasOwnProperty(step)) {
        const stepData = sectionData[step];

        steps.push(
          <div key={step}>
            <h5>{step}</h5>
            <h4>{stepData.stepTitle}</h4>
            <p>{stepData.content}</p>
            <img src={stepData.photoPath} />
          </div>
        )
      }
    }

    return steps;
  };
}

export default Assembly;


